// product page aside

.iOS {
    .cta {
        background: url("../img/product/cta.jpg") no-repeat center / cover;
    }
}

.cta {
    background: url("../img/product/cta.jpg") no-repeat center fixed / cover;
    position: relative;
    overflow: hidden;
    height: 450px;
    &_shape {
        @include  cover;
        z-index: 1;
        opacity: 0.61;
        top: -500px;
        left: -300px;
        width: 1000px;
        height: 1200px;
        img {
            height: 100%;
            width: auto;
        }
    }
    .container {
        position: relative;
        z-index: 2;
    }
    &_title {
        margin-bottom: 30px;
        line-height: .9;
        color: #fff;
        background: #FB4120;
    }
}

// media queries

// --- tablet
@media screen and (min-width: 767.98px) {
    .cta {
        &_shape {
            top: -700px;
            height: 1800px;
        }
    }
}

// --- desktop
@media screen and (min-width: 1769.98px) {
    .cta {
        &_shape {
            top: -1040px;
            left: -430px;
            width: 2500px;
            height: 2500px;
        }
    }
}