// suggested section component

.suggested {
    &.gradient {
        position: relative;
        background: #F3F3F3;
        overflow: hidden;

        .suggested_bg {
            @include cover;
            opacity: 0.31;
            z-index: 1;
            img {
                height: 100%;
                min-height: 900px;
            }
        }
    }

    .container {
        position: relative;
        z-index: 2;
    }

    &_slider {
        margin-top: 30px;
    }

}

